.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-top: 100px;
  background: $dark url('../img/hero-bg.jpg') no-repeat center / cover;
  height: 100vh;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($dark, 0.7); }
  &__instagram {
    max-width: 80px;
    height: 100vh;
    position: absolute;
    left: 0;
    top: 0; }
  &__scroll-down {
    position: absolute;
    left: 0;
    bottom: 0;
    min-width: 17.64rem;
    min-height: 6.93rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $dark;
    text-transform: uppercase;
    text-decoration: none;
    color: $light;
    &::after {
      content: '';
      background: url(../img/arrow-down.svg) no-repeat center;
      width: 30px;
      height: 30px;
      border: 2px solid $primary;
      border-radius: 50%;
      margin-left: 12px; } }
  &__text {
    color: $light;
    font-size: 1.571rem;
    max-width: 500px; }
  &__button {
    margin-right: 20px; }
  &__content {
    max-width: 47.857rem;
    position: relative;
    margin-left: 4.285rem; } }
.instagram {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $dark;
  &__link {
    display: flex;
    align-items: center;
    text-transform: uppercase;
    text-decoration: none;
    color: $primary;
    transform: rotate(90deg);
    &::after {
      content: '';
      width: 50px;
      height: 1px;
      background-color: $primary;
      margin-left: 8px; } } }
.title {
  margin: 0;
  font-family: 'Yeseva One', cursive;
  text-transform: uppercase;
  font-size: 2.857rem;
  color: $light;
  &--primary {
    color: $primary; } }
