@media screen and ( max-width: 1200px ) {
  html {
    font-size: 12px; }
  .nav {
    &__item {
      margin-right: 1rem; } } }
@media screen and ( max-width: 992px ) {
  .nav {
    display: none; }
  .hero {
    &__instagram {
      display: none; }
    &__content {
      margin-left: 0; } } }
@media screen and ( max-width: 760px ) {
  .section-title {
    &__heading {
      color: $light; } }
  .project {
    background-color: $dark-2; }
  .excerption {
    margin-bottom: 2.785rem;
    &__title {
      width: 21.85rem;
      height: 5.43rem;
      text-align: center;
      color: $light; } }
  .row {
    flex-direction: column;
    align-items: center; }
  .types {
    &__card {
      margin-bottom: 5rem; } }
  .card {
    &__text {
      margin-left: 0; } } }
@media screen and ( max-width: 576px ) {
  .contacts {
    &__phone {
      margin-right: 0;
      color: $light;
      text-decoration: none; }
    &__button {
      display: none; }
    .title {
      font-size: 1.64rem; } }
  .hero {
    &__scroll-down {
      display: none; }
    &__content {
      max-width: 22.86rem; }
    &__text {
      font-size: 1.29rem;
      margin-bottom: 2.28rem; }
    &__button {
      display: block;
      min-width: 22.86rem;
      margin-bottom: 1.85rem; } } }

