.button {
  padding: 13px 20px;
  border: none;
  border-radius: 2px;
  text-transform: uppercase;
  font-size: 0.93rem;
  &:hover {
    cursor: pointer; }
  &--primary {
    background-color: $primary;
    color: $light;
    box-shadow: 0px 5px 15px rgba($primary, 0.2);
    transition: box-shadow 0.3s;
    &:hover {
      box-shadow: none; } }
  &--outline {
    border: 1px solid $light;
    background-color: transparent;
    color: $light; } }
.policy {
  &__checkbox {
    opacity: 0;
    visibility: hidden;
    &:checked + {
      .policy__label {
        &::before {
         background: $primary url('../img/check.svg') no-repeat center / 0.8rem; } } } }
  &__label {
    display: flex;
    align-items: center;
    &::before {
      content: '';
      width: 18px;
      height: 18px;
      border: 1px solid $primary;
      background-repeat: 1px;
      cursor: pointer;
      margin-right: 0.64rem; } } }



